continued to become my senior year design project in UConn. During my research, I primarily used GNURadio software framework to create SDR modules capable of real-time inter-computer message transmissions. Technologies used included C++ (Boost/SWIG) to create GNURadio signal-processing modules, Python to create the application layer of the network, and MATLAB / LabVIEW to test signal transmissions. I continued on with this project after the NSF funded summer research program, and used it as my undergraduate senior design project. ")])])])],1),_c('v-row',[_c('v-col',[_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('reu-team').src,"aspect-ratio":"1.5","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('reu-team'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('reu-team').caption)+" ")])],1)]),_c('v-col',[_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('senior-design-team').src,"aspect-ratio":"1.5","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('senior-design-team'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('senior-design-team').caption)+" ")])],1)])],1),_c('v-row',[_c('v-col',[_c('div',{staticClass:"header-2 mt-10 mb-4"},[_vm._v(" Background ")]),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" The generic SDR hardware unit used in this project was the Universal Software Radio Peripheral (USRP) manufactured by National Instruments. The USRP functions using an FPGA, and consists of transmitter (Tx) and receiver (Rx) antennas that are used to send and receive signals. These hardware units also include several subsystems such as a clocks, ADCs, and DACs needed for data conversion. ")]),_c('p',[_vm._v(" The implementation of the ad-hoc wireless communication system consisted of a PHY layer that uses Offset Quadrature Phase Shift Keying (O-QPSK) modulation/demodulation scheme, operating at 2.4 GHz frequency. On top of the PHY layer lies the MAC layer. The MAC Layer works with MAC Frames, a standardized sequence of bytes for structured data handling. Time-division Multiple Access (TDMA) method was implemented in the MAC layer. TDMA divides a time frame into slots, assigning a specific slot to a device. Device are only allowed to communicate during their specified slots in order to avoid packate collisions. Encryptions of the payloads were added in the MAC layer using AES CBC block cipher. ")])])])],1),_c('v-row',[_c('v-col',[_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('impl-overview').src,"aspect-ratio":"2.0","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('impl-overview'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('impl-overview').caption)+" ")])],1)]),_c('v-col',[_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('full-lab-setup').src,"aspect-ratio":"2.0","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('full-lab-setup'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('full-lab-setup').caption)+" ")])],1)])],1),_c('v-row',[_c('v-col',[_c('div',{staticClass:"header-2 mt-5 mb-4"},[_vm._v(" Physical Layer ")]),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" The PHY layer of the system follows IEEE 802.15.4 standards in order to perform modulation - demodulation. GNURadio block arrangements to implement the standardized PHY layer operations was taken from "),_c('a',{attrs:{"href":"https://www.wime-project.net/","target":"_blank"}},[_vm._v(" Wireless Measurement and Experimentation (WIME) ")]),_vm._v(" project's 802.15.4 (Zigbee) transceiver. ")]),_c('p',[_vm._v(" The IEEE 802.15.4 physical layer works by adding additional header and paddings to the barebone signal. Each byte of the signal is then divided into symbols of 4 bits, and each symbol mapped to one out of the 16 pseudo-random sequences defined in the standard, 