equency and phase as those used by the transmitted. This is necessary, for instance, when extracting symbols from an asynchronous digital signal. It allows to synchronize the receiver with centers of ones and zeros present in the signal. ")])]),_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('phy-gnuradio-receiver').src,"aspect-ratio":"10.0","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('phy-gnuradio-receiver'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('phy-gnuradio-receiver').caption)+" ")])],1),_c('div',{staticClass:"header-2 mt-5 mb-4"},[_vm._v(" MAC Layer ")]),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" The MAC layer formatting of payloads follows the IEEE 802.15.4 standard for most part. When a payload arrives at the MAC layer, it is formatted with a header that consists of an FCF (Frame Control Format) number, a sequence number, and source/destination addresses. The payload is then padded with a CRC which is later on used to detect errors and corruptions in the packet in the receiver end. ")])]),_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('mac-packet-struct').src,"aspect-ratio":"7.5","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('mac-packet-struct'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('mac-packet-struct').caption)+" ")])],1),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" The IEEE 802.15.4 standard supports but does not require collision detection and avoidance between packets. In order to implement scheduled transmissions of packets throughout different senders, Time-Division Multiple Access (TDMA) protocol was used. TDMA is a channel access method that allows several devices to communicate on the same frequency without causing too much of an interference or collision of the packets in air. This increases the throughput of the network by coordinating all transmitters and allowing them to transmit seamlessly on the same frequency. TDMA achieves this by taking a selected period of time and dividing it into several slots. The collection of these slots is called a “superframe”. ")])]),_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('mac-superframe-struct').src,"aspect-ratio":"4.5","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('mac-superframe-struct'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('mac-superframe-struct').caption)+" ")])],1),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" The superframe format was designed to be 1.28 seconds long and was divided into 128 slots, each slot being 10 milliseconds long. ")]),_c('p',[_vm._v(" A coordinator node in the network plays the role of maintaining a universal clock and assigning time-slots to the senders. The coordinator achieves it by broadcasting a beacon at the start of each of superframe in order to establish the synchronization between multiple senders. This universal time is maintained in the GNURadio beacon module with a POSIX thread that runs throughout the lifespan of the coordinator node. ")]),_c('p',[_vm._v(" When a new sender module in the network receives this beacon message, it would have the next six time slots of the superframe to send a join request, essentially asking the coordinator to assign it a time-slot. This lets the coordinator know that a new transmitter wants to join the network and needs a slot, so at the seventh superframe slot, the coordinator will send that transmitter an association message. The message contains the slot number, starting from 8 and going all the way to 127, in which the sender is allowed to transmit its data. ")]),_c('p',[_vm._v(" That sender now listens for the next beacon. Upon receiving a beacon at the beginning of a superframe, it makes note of the time the beacon was sent and calculates at what time it has to transmit its own message. When the slot arrives, the sender then can then transmit its data without worrying about collisions with other senders. ")])]),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" The functions of the "),_c('strong',[_vm._v("coordinator")]),_vm._v(" for this system can be summarized with three distinct duties: ")]),_c('ul',[_c('li',[_vm._v("Transmit a beacon at the beginning of each superframe")]),_c('li',[_vm._v("Listen for join requests from new senders")]),_c('li',[_vm._v("Appropriately send slot-associations to the senders")])])]),_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('mac-gnuradio-coordinator').src,"aspect-ratio":"2.6","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('mac-gnuradio-coordinator'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('mac-gnuradio-coordinator').caption)+" ")])],1),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" The "),_c('strong',[_vm._v("sender")]),_vm._v(" for this system performs the following tasks ")]),_c('ul',[_c('li',[_vm._v("Newly joined sender sends a join request upon receiving the beacon")]),_c('li',[_vm._v("Listens for an association from the coordinator")]),_c('li',[_vm._v(" After getting an assigned time-slot, it can transmit messages to the receiver at the appropriate time ")])])]),_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('mac-gnuradio-sender').src,"aspect-ratio":"2.7","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('mac-gnuradio-sender'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('mac-gnuradio-sender').caption)+" ")])],1),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" Lastly, the implementation of the "),_c('strong',[_vm._v("receiver")]),_vm._v(" is fairly straightforward. It listens for messages coming into the USRP Rx, passes it to the physical layer for demodulation upon receiving, and analyzes the message in the MAC_Receiver block. ")])]),_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('mac-gnuradio-receiver').src,"aspect-ratio":"2.3","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('mac-gnuradio-receiver'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('mac-gnuradio-receiver').caption)+" ")])],1),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" Each of the MAC blocks also checks the destination and source of the received signal, and checks the FCF to confirm the type of the message. The MAC blocks also perform the CRC check on the received signals, and incorporates the code to encrypt the payloads of the sent messages and decrypt the payload of the received messages. ")])]),_c('div',{staticClass:"header-2 mt-5 mb-4"},[_vm._v(" Observations ")]),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" In order to test the distance vs. packet loss ratio (PLR) of the system, a python script was used to connect to GNURadio via sockets. The script transmitted 1000 messages via the transmitter, and on the receiver side the script maintained a count of how many messages were successfully received. The transmitter and receiver distance were then gradually increased to map out how the PLR gets affected based on the distance. ")])]),_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('distance-vs-packet-loss-ratio-plot').src,"aspect-ratio":"1.55","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('distance-vs-packet-loss-ratio-plot'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('distance-vs-packet-loss-ratio-plot').caption)+" ")])],1),_c('div',{staticClass:"text justify-text"},[_c('p',[_vm._v(" From the data, it can be seen that up until 15 ft. of distance between the sender and receiver, the PLR remained fairly low, and from there on it kept on increasing exponentially. Note that no power amplifier was used in the test, which is one of the main reasons why the PLR was affected so much after a distance on 15 ft. ")]),_c('p',[_vm._v(" The next test calculated PLR against the number of senders present in the network with two differen MAC protocols: a barebone IEEE 802.15.4 MAC with no particular scheduling scheme, and the TDMA MAC discussed above that incorporated a coordinator-sender synchronization to routinely send messages. For one sender and one receiver setting, the system with TDMA enabled performed about the same as the barebone IEEE 802.15.4 MAC. However, for two and three sender scenarios, the TDMA performed almost twice as much better, as they had half PLR compared the system with no scheduling schemes. ")])]),_c('div',{staticClass:"img"},[_c('v-img',{attrs:{"src":_vm.getMediaObject('tdma-packet-loss-ratio-plot').src,"aspect-ratio":"1.65","max-width":"600"},on:{"click":() => { _vm.showLightboxImage('tdma-packet-loss-ratio-plot'); }}}),_c('span',{staticClass:"footnote-2"},[_vm._v(" "+_vm._s(_vm.getMediaObject('tdma-packet-loss-ratio-plot').caption)+" ")])],1)])],1),_c('lightbox',{ref:"lightbox",attrs:{"media":_vm.media,"show-light-box":false,"show-caption":false,"disable-scroll":"","show-thumbs":""}})],1)],1)
